<template>
    <div class="all-wrapper rentals">
        <div class="property-single">

            <cover :img="program.cover"/>

            <div class="property-info-w">

                <main-section  />

                <side-section header="Inscrição" class="text-center">
                    <subscribe @onSubscribe="subscribe"></subscribe>
                </side-section>

            </div>

            <!--<div class="related-listings-w">-->
            <!--<h2 class="property-section-big-header">-->
            <!--Programas Relacionados-->
            <!--</h2>-->
            <!--<div class="related-listings">-->
            <!--<card-list :limit-number-of-programs="3"></card-list>-->
            <!--</div>-->
            <!--</div>-->
        </div>
    </div>
</template>
<script>

    import Cover from './components/Cover'
    import SideSection from './components/SideSection'
    import MainSection from './components/MainSection'
    import Subscribe from './components/Subscribe'
    import Card from '../../../components/CustomCard'
    import FooterComponent from '../components/Footer'
    import programsMixis from '../../../mixins/programs'
    import CardList from "./CardList";
    import AuthMixins from '../../../mixins/auth'

    export default {
        mixins: [programsMixis, AuthMixins],
        components: {
            CardList,
            Cover,
            SideSection,
            Subscribe,
            Card,
            FooterComponent,
            MainSection
        },
        created() {
            this.preload(true);
            this.getProgram({id: this.$route.params.id}).then(() => this.preload(false));
        },
        methods: {
            subscribe() {
                if (this.isAuthenticated) {
                    /*futuramente vai pra rota de proposta*/
                    this.$router.push({
                        name: 'admin.program.subscribe',
                        params:{
                            id:this.$route.params.id
                        }
                    });
                } else {
                    this.$router.push({
                        name: 'login'
                    });
                }
            }
        }

    }
</script>
<style>
    .content-box {
        padding: 0;
    }
</style>
