<template>
    <div class="property-info-main">
        <div class="property-price">
            <img :src="program.company.data.photo_url" v-if="program.company"/>
        </div>
        <div class="badge badge-primary" v-for="market in program.market" v-text="market.name">
        </div>
        <h1>
            {{program.title}}
        </h1>
        <!--featured value here-->

        <!--featured value -->

        <div class="property-description">
            <!--about market -->
            <p>
                {{program.description}}
            </p>
        </div>

        <div class="times">
            <div class="timed-activities compact">
                <div class="timed-activity">
                    <div class="ta-date">
                        <span>Início do programa {{fromTs(program.program_start ).toFormat('dd/MM/yyyy')}}  </span>
                        <br>
                        <span>Fim do prgrama  {{fromTs(program.program_end ).toFormat('dd/MM/yyyy')}}  </span>
                        <br>
                        <span>Início do programa {{fromTs(program.date_start ).toFormat('dd/MM/yyyy')}}    </span>
                        <br>
                        <span>Fim do prgrama {{fromTs(program.date_end ).toFormat('dd/MM/yyyy')}} </span>
                        <br>
                    </div>
                </div>
            </div>
        </div>

        <div class="property-section">
            <div v-html="program.about"></div>
        </div>
    </div>
</template>
<script>
    import dateMixins from '../../../../mixins/dates'
    import programsMixis from '../../../../mixins/programs'

    export default {
        mixins:[dateMixins,programsMixis],
    }
</script>
<style>
    img{
        width: 50%;
        border-radius: 50%;
    }
</style>