<template>
    <div class="element-wrapper">
        <div class="element-box text-center">
            <h3 class="element-header"> Quero me inscrever</h3>
            <p >
                <p-check name="check" color="success" v-model="accept" class="p-svg p-curve">
                    <svg slot="extra" class="svg svg-icon" viewBox="0 0 20 20">
                        <path d="M7.629,14.566c0.125,0.125,0.291,0.188,0.456,0.188c0.164,0,0.329-0.062,0.456-0.188l8.219-8.221c0.252-0.252,0.252-0.659,0-0.911c-0.252-0.252-0.659-0.252-0.911,0l-7.764,7.763L4.152,9.267c-0.252-0.251-0.66-0.251-0.911,0c-0.252,0.252-0.252,0.66,0,0.911L7.629,14.566z"
                              style="stroke: white;fill:white"></path>
                    </svg>
                    Li e entendi os termos do programa.</p-check>

            </p>
                <p class="text-danger m-t m-b" v-if="error">Para se inscrever você deve aceitar os termos</p>
            <button @click="emitSubscribe"  class="mr-2 mb-2 btn btn-primary btn-lg text-uppercase"><small>Inscrever-se</small></button>
        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                accept: false,
                error: false
            }
        },
        methods: {
            emitSubscribe() {
                if (this.accept) {
                    this.$emit('onSubscribe')
                } else {
                    this.error = true
                }
            },

        },
        watch:{
            accept(){
                this.accept ? this.error = false :this.error = true;
            }
        }

    }

</script>